import database from "./database";
import HOTEL from "./Tables/HOTEL";

class HotelData {
    constructor() {
        this.db = new database();
    }

    getHotel() {
        this.db.getDB().transaction(function (transaction) {
            transaction.executeSql(("SELECT * FROM " + HOTEL.TABLE_NAME + " WHERE 1 LIMIT 1"), [], function (transaction, results) {
                console.log(results);
                return {status: results.rows > 0, data: results};
            }, function (transaction, error) {
                console.log(error);
                return {status: false, message: error.message};
            });
        });
    }

    saveHotel() {
        this.db.getDB().transaction(function (transaction) {
            transaction.executeSql(("INSERT INTO " + HOTEL.TABLE_NAME + " (winklename, location) VALUES (?, ?);"), [1, "2"], function (transaction, results) {
                return results;
            }, function (transaction, error) {
                console.log(error);
                return error;
            });
        });
    }
}

export default HotelData;