import CLIENT from "./Tables/CLIENT";
import ABONNEMENT from "./Tables/ABONNEMENT";
import APPAREIL from "./Tables/APPAREIL";
import BEDROOM from "./Tables/BEDROOM";
import FORMULE from "./Tables/FORMULE";
import HOTEL from "./Tables/HOTEL";
import OCCUPATION from "./Tables/OCCUPATION";
import OCCUPATION_SERVICE from "./Tables/OCCUPATION_SERVICE";
import PAIEMENT from "./Tables/PAIEMENT";
import RESERVATION from "./Tables/RESERVATION";
import SERVICE from "./Tables/SERVICE";
import USER from "./Tables/USER";

class Database {

    constructor() {
        this.db = openDatabase("tchede_hotel", "1.0", "Tchede Hotel App", 32678);
    }

    success(data) {

    }

    error(error) {

    }

    makeDB() {
        // Create winkles table if required
        this.db.transaction(function(transaction){
            transaction.executeSql(CLIENT.CREATE_TABLE);
            transaction.executeSql(ABONNEMENT.CREATE_TABLE);
            //transaction.executeSql(APPAREIL.CREATE_TABLE);
            transaction.executeSql(BEDROOM.CREATE_TABLE);
            transaction.executeSql(FORMULE.CREATE_TABLE);
            transaction.executeSql(HOTEL.CREATE_TABLE);
            transaction.executeSql(OCCUPATION.CREATE_TABLE);
            transaction.executeSql(OCCUPATION_SERVICE.CREATE_TABLE);
            transaction.executeSql(PAIEMENT.CREATE_TABLE);
            transaction.executeSql(RESERVATION.CREATE_TABLE);
            transaction.executeSql(SERVICE.CREATE_TABLE);
            transaction.executeSql(USER.CREATE_TABLE);
        });
    }

    getDB() {
        return this.db;
    }
}

export default Database;